import { IPermissionsModel } from '../../../shared/fleio-api/base-model/IPermissionsModel';
import { IIcon } from '../../../shared/ui/common/interfaces/icon';
import { ITitle } from '../../../shared/ui/objects-view/interfaces/card-data/card-title';
import { IObjectStatus, StatusType, StatusValue } from '../../../shared/ui/objects-view/interfaces/object-status';
import { ObjectUIServiceBase } from '../../../shared/ui/objects-view/object-ui-service-base';
import { IAction } from '../../../shared/ui/objects-view/interfaces/actions/action';
import { RouterLinkAction } from '../../../shared/ui/objects-view/actions/router-link-action';
import { Router } from '@angular/router';
import { ConfigService } from '../../../shared/config/config.service';
import { IDataField } from '../../../shared/ui/objects-view/interfaces/card-data/data-field';
import { ApiCallAction, CallType } from '../../../shared/ui/objects-view/actions/api-call-action';
import { IDetailsTab } from '../../../shared/ui/objects-view/interfaces/details/details-tab';
import { DatePipe } from '@angular/common';
import { CallbackAction } from '../../../shared/ui/objects-view/actions/callback-action';
import { ImagesApiService } from '../../../shared/fleio-api/cloud/image/image-api.service';
import { IImageModel } from '../../../shared/fleio-api/cloud/model/image.model';
import { ImageDetailsOverviewComponent } from './tabs/image-details-overview/image-details-overview.component';
import { ImageEditFormComponent } from './tabs/image-edit-form/image-edit-form.component';

export class ImageUiService extends ObjectUIServiceBase<IImageModel> {
  private readonly router: Router;
  private readonly config: ConfigService;
  private readonly imagesApiService: ImagesApiService;
  private readonly datePipe: DatePipe;


  constructor(
    image: IImageModel, permissions: IPermissionsModel, state: string,
    router: Router, config: ConfigService, imagesApiService: ImagesApiService
  ) {
    super(image, permissions, state);
    this.router = router;
    this.config = config;
    this.imagesApiService = imagesApiService;
    this.datePipe = new DatePipe(this.config.locale);
  }

  getIcon(): IIcon {
    return {
      class: 'fl-icons',
      name: this.object.os_distro || 'otheros',
    };
  }

  getStatus(): IObjectStatus {
    switch (this.object.status) {
      case 'deactivated':
        return {
          type: StatusType.Defined,
          value: StatusValue.Deactivated,
        };
      case 'active':
      default:
        return {
          type: StatusType.Defined,
          value: StatusValue.Active,
        };
    }
  }

  getTitle(): ITitle {
    let prefix = '';
    if (this.object.type === 'snapshot') {
      prefix = 'snapshot';
    }

    if (this.object.disk_format === 'iso') {
      prefix = 'ISO';
    }

    if (prefix.length > 0) {
      prefix = `[${prefix}] `;
    }

    switch (this.state) {
      case 'edit':
        return {
          text: `Edit ${this.object.name || this.object.id}`,
          subText: this.object.status ? this.object.status.toLocaleUpperCase() : 'n/a',
        };

      case 'create':
        return {
          text: `Create new image`,
        };

      case 'details':
      default:
        return {
          text: `${prefix}${this.object.name || this.object.id}`,
          subText: this.object.status ? this.object.status.toLocaleUpperCase() : 'n/a',
        };
    }
  }

  getActions(): IAction[] {
    const actions: IAction[] = [];

    actions.push(new RouterLinkAction({
        icon: {name: 'edit', class: 'fl-icons'},
        name: 'Edit',
        tooltip: 'Edit image',
        routerUrl: this.config.getPanelUrl(`cloud/images/${this.object.id}/edit`),
        router: this.router,
      }
    ));

    actions.push(new ApiCallAction(
      {
        object: this.object,
        icon: {name: 'delete'},
        name: 'Delete',
        tooltip: 'Delete image',
        confirmOptions: {
          confirm: true,
          title: 'Delete image',
          message: `Are you sure you want to delete image ${this.object.name}`,
        },
        apiService: this.imagesApiService,
        callType: CallType.Delete,
      }
    ));

    return actions;
  }

  getDetailsActions(): IAction[] {
    const actions = [];

    switch (this.state) {
      case 'create':
        actions.push(new RouterLinkAction({
            name: 'Cancel',
            routerUrl: this.config.getPrevUrl(`cloud/images`),
            router: this.router,
          }
        ));
        actions.push(new CallbackAction({name: 'Create'}));
        break;
      case 'edit':
        actions.push(new RouterLinkAction({
            name: 'Cancel',
            routerUrl: this.config.getPrevUrl(`cloud/images`),
            router: this.router,
          }
        ));
        actions.push(new CallbackAction({name: 'Save'}));
        break;
      default:
        break;
    }

    return actions;
  }

  getDetailsLink(): string {
    return this.config.getPanelUrl(`cloud/images/${this.object.id}`);
  }

  getCardFields(): IDataField[] {
    const fields = [
      {
        value: `${this.object.min_disk} GB min. disk, ${this.object.region}`
      },
      {
        name: 'Created:',
        value: this.datePipe.transform(this.object.created_at),
      },
    ];

    return fields;
  }

  getTabs(): IDetailsTab[] {
    switch (this.state) {
      case 'details':
        return [
          {
            tabName: 'Overview',
            component: ImageDetailsOverviewComponent,
          },
        ];
      case 'create':
        return [
          {
            tabName: 'Create',
            component: ImageEditFormComponent,
          },
        ];
      case 'edit':
        return [
          {
            tabName: 'Create',
            component: ImageEditFormComponent,
          },
        ];
    }
  }

  getCardTags(): string[] {
    const tags: string[] = [];
    if (this.object.protected) {
      tags.push('protected');
    }
    return tags;
  }
}
