import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { IRouteConfig } from '../../../shared/ui-api/interfaces/route-config/route-config';
import { ImageCreateComponent } from './image-create/image-create.component';
import { ImageEditComponent } from './image-edit/image-edit.component';
import { ImageDetailsComponent } from './image-details/image-details.component';
import { ImageListComponent } from './image-list/image-list.component';
import { ImageListResolver } from '../../../shared/fleio-api/cloud/image/image-list.resolver';
import { ImageResolver } from '../../../shared/fleio-api/cloud/image/image.resolver';
import { ImagePermissionsResolver } from '../../../shared/fleio-api/cloud/image/image-permissions.resolver';
import { ImageCreateOptionsResolver } from '../../../shared/fleio-api/cloud/image/image-create-options.resolver';
import { IBaseFleioObjectModel } from '../../../shared/fleio-api/base-model/base-fleio-object.model';
import { FleioObjectsList } from '../../../shared/fleio-api/fleio-objects-list';
import { OrderingDirection } from '../../../shared/ui-api/interfaces/route-config/ordering-directions';
import { AuthGuard } from '../../../shared/auth/auth.guard';

const routes: Routes = [
  {
    path: '',
    component: ImageListComponent,
    resolve: {
      images: ImageListResolver,
    },
    canActivate: [AuthGuard],
    data: {
      config: {
        feature: 'openstack.images',
        search: {
          show: true,
          placeholder: 'Search images ...',
        },
        subheader: {
          objectList(data): FleioObjectsList<IBaseFleioObjectModel> {
            return data.images;
          },
          objectName: 'image',
          objectNamePlural: 'images'
        },
        ordering: {
          default: {
            display: 'Created at',
            field: 'created_at',
            direction: OrderingDirection.Descending,
          },
          options: [
            {display: 'Name', field: 'name'},
            {display: 'Status', field: 'status'},
            {display: 'Type', field: 'type'},
            {display: 'Created at', field: 'created_at'},
          ]
        }
      } as IRouteConfig,
    },
    runGuardsAndResolvers: 'always'
  },
  {
    path: 'create',
    component: ImageCreateComponent,
    resolve: {
      createOptions: ImageCreateOptionsResolver,
    },
    data: {
      config: {
        getBreadCrumbDetail: () => {
          return 'Create image';
        },
      } as IRouteConfig,
    }
  },
  {
    path: ':id',
    component: ImageDetailsComponent,
    resolve: {
      image: ImageResolver,
      permissions: ImagePermissionsResolver,
    },
    data: {
      config: {
        getBreadCrumbDetail: (data) => {
          return data.image.name || data.image.id;
        },
      } as IRouteConfig,
    }
  },
  {
    path: ':id/edit',
    component: ImageEditComponent,
    resolve: {
      image: ImageResolver,
      createOptions: ImageCreateOptionsResolver,
    },
    data: {
      config: {
        getBreadCrumbDetail: (data) => {
          return data.image.name || data.image.id;
        },
      } as IRouteConfig,
    }
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ImagesRoutingModule {
}
